@-webkit-keyframes giftAnimated {
    0%, 100% { transform: translateY(0px); }
    50% { transform: translateY(-15px); }
}

@keyframes giftAnimated {
    0%, 100% { transform: translateY(0px); }
    50% { transform: translateY(-15px); }
}

@-webkit-keyframes heartAnimated {
    0%, 100% { transform: translateY(5px); }
    50% { transform: translateY(-15px); }
}

@keyframes heartAnimated {
    0%, 100% { transform: translateY(5px); }
    50% { transform: translateY(-15px); }
}

svg > g:nth-child(8) {
    -webkit-animation: giftAnimated 4s linear infinite;
            animation: giftAnimated 4s linear infinite;
}

svg > g:nth-child(9) {
    -webkit-animation: heartAnimated 6s linear infinite;
            animation: heartAnimated 6s linear infinite;
}
@-webkit-keyframes boxAnimated {
    0%, 100% { transform: translateY(0px); }
    50% { transform: translateY(-15px); }
}

@keyframes boxAnimated {
    0%, 100% { transform: translateY(0px); }
    50% { transform: translateY(-15px); }
}

svg > g:nth-child(3) {
    -webkit-animation: boxAnimated 4s linear infinite;
            animation: boxAnimated 4s linear infinite;
}
